import { request } from "@/http"

/**
 * 1.getToken
 */
export function indexGetToken(params = {}) {
  return request({
    url: "api/index/getToken",
    method: "get",
    data: params,
  })
}

/**
 * 2.发送邮件
 */
export function emsSend(params = {}) {
  return request({
    url: "api/ems/send2",
    method: "post",
    data: {
      ...params,
      is_test: import.meta.env.VITE_APP_TEST,
    },
  })
}

