<script setup>
import MatchModel from "@/components/MatchModel"
import { useUserStore } from "@/store/useUserStore"
import { ref, watch, createApp } from "vue"
import { usePollingTask } from "@/hooks/usePolling"
import { indexMatch } from "@/api/totalApi.js"

const app = createApp(MatchModel)
let modelInstance = null

const userStore = useUserStore()
let stopTaskFun = null

// 用于存储已处理的匹配ID
const processedMatchIds = ref(new Set())

const isInitMatchList = ref(true)

// ------------------ 轮询任务初始化封装 ------------------
const setupMatchPolling = () => {
  // 未登录或已有轮询时不处理
  if (!userStore.isLogin || stopTaskFun) return

  // 获取匹配状态
  const { startTask, stopTask, onSuccess } = usePollingTask(
    () => indexMatch({ rows: 999999999 }),
    2000,
  )

  // 封装停止函数，方便外部调用并重置状态
  stopTaskFun = () => {
    stopTask()
    stopTaskFun = null
    processedMatchIds.value.clear()
    isInitMatchList.value = true
  }

  onSuccess((result) => {
    if (result?.code === 200 && Array.isArray(result?.data?.data)) {
      const matches = result.data.data

      // 标记是否为首轮拉取（首轮不弹窗，只初始化数据）
      const isFirstFetch = isInitMatchList.value
      if (isFirstFetch) {
        isInitMatchList.value = false
      }

      matches.forEach((match) => {
        // 新ID
        if (!processedMatchIds.value.has(match.id)) {
          processedMatchIds.value.add(match.id)

          // 首轮之后的任何新数据都要弹窗
          if (!isFirstFetch) {
            console.log("打开匹配弹窗")
            uni.$emit("showMatchModal", match)
          }
        }
      })
    }
  })

  // 启动轮询
  startTask()
}

// 监听登录状态变化：登录后启动轮询，登出后停止轮询
watch(
  () => userStore.isLogin,
  (isLoginNow) => {
    if (isLoginNow) {
      setupMatchPolling()
    } else if (stopTaskFun) {
      stopTaskFun()
    }
  },
)

onLaunch(async () => {
  uni.setLocale("en")
  // 先检查是否有弹窗实例了
  const element = document.querySelector("#match-modal-container")
  if (element || modelInstance) {
    return
  }

  const modalContainer = document.createElement("match-modal")
  modalContainer.id = "match-modal-container"
  document.body.appendChild(modalContainer)
  modelInstance = app.mount(modalContainer)

  // 页面加载完成后尝试启动轮询（若已登录）
  setupMatchPolling()
})
</script>
