import { createSSRApp } from "vue"
import App from "./App.vue"

import "virtual:uno.css"

import * as Pinia from "pinia"
import piniaPersist from "pinia-plugin-persist-uni"
import { prototypeInterceptor, requestInterceptor } from "@/interceptors"

// import "@/static/tailwind.css"

import { routeInterceptor } from "@/interceptors/route"

import MatchModel from "@/components/MatchModel"

import { Locale } from '@/uni_modules/wot-design-uni'
// 引入英文语言包
import enUS from '@/uni_modules/wot-design-uni/locale/lang/en-US'

Locale.use('en-US', enUS)

const messages = {
  'en-US': {
    picker: {
      done: 'Complete' // 将'选择日期'修改为'请选择日期'
    }
  }
}
Locale.add(messages)


uni.$zp = {
  config: {
    'show-loading-more-when-reload': false,
    'loading-more-no-more-text': '',
    'loading-more-loading-text': '',
    'loading-more-default-text': '',
    'hide-no-more-inside': true,
    'show-loading-more-no-more-view': false,
    'show-default-loading-more-text': false,
    'show-loading-more-default-view': false,
  }
}

export function createApp() {
  const app = createSSRApp(App)
  // 注册pinia持久化
  const pinia = Pinia.createPinia()
  pinia.use(piniaPersist)
  // 注册拦截器
  app.use(pinia)
  app.use(requestInterceptor)
  app.use(prototypeInterceptor)
  app.use(routeInterceptor)

  app.component("MatchModel", MatchModel)

  return {
    app,
    Pinia,
  }
}
