<script setup>
import { ref } from "vue"

const showModal = ref(false)
const targetMatch = ref({})

uni.$on("showMatchModal", (event) => {
  showModal.value = true
  targetMatch.value = event
  console.log("收到新匹配: ", event)
})

onUnmounted(() => {
  uni.$off("showMatchModal")
})

const handleClickCheckIt = () => {
  showModal.value = false
  useNavigate("/pages/tracking/detail", { id: targetMatch.value?.order_id })
}
</script>

<template>
  <wd-popup
    v-model="showModal"
    position="center"
    custom-style="border-radius: 8px"
  >
    <view class="match-wrapper">
      <view class="header">
        <view class="close-icon-button" @click="showModal = false">
          <wd-icon name="close" color="#ffffff" size="16px" />
        </view>
      </view>
      <view class="content">
        <p>You have a new</p>
        <p>resources match!</p>
      </view>
      <view class="bottom-line"></view>
      <view class="bottom-box">
        <view class="check-out-button" @click="handleClickCheckIt">
          Check it out!
        </view>
      </view>
    </view>
  </wd-popup>
</template>

