import { defineStore } from "pinia"
import { onUserLogout } from "@/api/common"

const initState = {
  avatar: "",
  createtime: 0,
  expires_in: 0,
  expiretime: 0,
  id: 0,
  mobile: "",
  nickname: "",
  score: 0,
  token: "",
  user_id: 0,
  username: "",
} //用户信息

// 定义store
export const useUserStore = defineStore(
  "user",
  () => {
    const userInfo = ref(initState)

    const setUserInfo = (val) => {
      userInfo.value = val
    }

    const onGetUserInfo = async () => {
      const res_userInfo = await indexGetUserinfo()
      setUserInfo(res_userInfo.data)
    }

    const clearUserInfo = () => {
      userInfo.value = {
        avatar: "",
        createtime: 0,
        expires_in: 0,
        expiretime: 0,
        id: 0,
        mobile: "",
        nickname: "",
        score: 0,
        token: "",
        user_id: 0,
        username: "",
      }
    }

    const isLogin = computed(() => !!userInfo.value.token)

    const onLogout = async () => {
      await onUserLogout()
      clearUserInfo()
      await useRedirectTo("/pages/login/index", {})
    }

    const setToken = token => {
      userInfo.value.token = token
    }

    return {
      userInfo,
      isLogin,
      setUserInfo,
      clearUserInfo,
      onGetUserInfo,
      onLogout,
      setToken
    }
  },
  {
    persist: {
      enabled: true,
      strategies: [
        {
          storage: {
            getItem: (key) => {
              return uni.getStorageSync(key)
            },
            setItem: (key, value) => {
              uni.setStorageSync(key, value)
            },
          },
        },
      ],
    },
  },
)
